'use client'

import { useState, useEffect } from 'react'
import { getGenres, getAnimeByGenre, Anime } from '@/lib/api'
import { Navbar } from '@/components/navbar'
import { AnimeCard } from '@/components/anime-card'
import { SkeletonGrid } from '@/components/skeleton-loader'

interface Genre {
  mal_id: number
  type: string
  name: string
  url: string
  count: number
}

export default function GenresPage() {
  const [genres, setGenres] = useState<Genre[]>([])
  const [selectedGenre, setSelectedGenre] = useState<number | null>(null)
  const [animes, setAnimes] = useState<Anime[]>([])
  const [loading, setLoading] = useState(true)

  useEffect(() => {
    const fetchGenres = async () => {
      const data = await getGenres()
      setGenres(data)
      if (data.length > 0) {
        setSelectedGenre(data[0].mal_id)
      }
      setLoading(false)
    }
    fetchGenres()
  }, [])

  useEffect(() => {
    if (!selectedGenre) return

    const fetchByGenre = async () => {
      setLoading(true)
      const data = await getAnimeByGenre(selectedGenre)
      setAnimes(data.data)
      setLoading(false)
    }

    fetchByGenre()
  }, [selectedGenre])

  return (
    <main className="min-h-screen bg-background">
      <Navbar />

      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-12">
        <h1 className="text-4xl font-bold mb-8 text-foreground">Browse by Genre</h1>

        {/* Genre Buttons */}
        <div className="mb-12">
          <h2 className="text-lg font-semibold mb-4 text-foreground">Select Genre</h2>
          <div className="flex flex-wrap gap-3">
            {genres.slice(0, 12).map((genre) => (
              <button
                key={genre.mal_id}
                onClick={() => setSelectedGenre(genre.mal_id)}
                className={`px-4 py-2 rounded-lg font-medium transition-all ${
                  selectedGenre === genre.mal_id
                    ? 'bg-primary text-white neon-glow'
                    : 'bg-card border border-border text-foreground hover:border-primary'
                }`}
              >
                {genre.name}
              </button>
            ))}
          </div>
        </div>

        {/* Results */}
        {loading ? (
          <SkeletonGrid />
        ) : (
          <>
            <h2 className="text-2xl font-semibold mb-6 text-foreground">
              {genres.find(g => g.mal_id === selectedGenre)?.name} Anime
            </h2>
            <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 xl:grid-cols-5 gap-4 md:gap-6">
              {animes.map((anime) => (
                <AnimeCard key={anime.mal_id} anime={anime} />
              ))}
            </div>
          </>
        )}
      </div>
    </main>
  )
}
