// API utilities for anime data fetching using Jikan API
const JIKAN_API_BASE = 'https://api.jikan.moe/v4'

import { rateLimit } from './rate-limit'

export interface Anime {
  mal_id: number
  title: string
  title_english?: string
  images: {
    jpg: {
      image_url: string
      small_image_url: string
      large_image_url: string
    }
  }
  synopsis: string
  episodes: number | null
  status: string
  rating: string
  score: number
  scored_by: number
  genres: Array<{ mal_id: number; type: string; name: string; url: string }>
  year: number | null
  season: string | null
  airing: boolean
  aired?: {
    from: string
    to: string | null
  }
  studios?: Array<{ mal_id: number; type: string; name: string; url: string }>
  url: string
}

export interface AnimeResponse {
  data: Anime[]
  pagination?: {
    last_visible_page: number
    has_next_page: boolean
    current_page: number
  }
}

// Fetch top anime
export async function getTopAnime(page = 1): Promise<AnimeResponse> {
  try {
    await rateLimit()
    const response = await fetch(
      `${JIKAN_API_BASE}/top/anime?page=${page}&limit=25&type=tv,movie&filter=airing`,
      { next: { revalidate: 3600 } }
    )
    if (!response.ok) throw new Error('Failed to fetch top anime')
    return response.json()
  } catch (error) {
    console.error('Error fetching top anime:', error)
    return { data: [] }
  }
}

// Fetch popular anime
export async function getPopularAnime(page = 1): Promise<AnimeResponse> {
  try {
    await rateLimit()
    const response = await fetch(
      `${JIKAN_API_BASE}/anime?order_by=popularity&sort=asc&page=${page}&limit=25`,
      { next: { revalidate: 3600 } }
    )
    if (!response.ok) throw new Error('Failed to fetch popular anime')
    return response.json()
  } catch (error) {
    console.error('Error fetching popular anime:', error)
    return { data: [] }
  }
}

// Fetch anime by genre
export async function getAnimeByGenre(genreId: number, page = 1): Promise<AnimeResponse> {
  try {
    await rateLimit()
    const response = await fetch(
      `${JIKAN_API_BASE}/anime?genres=${genreId}&page=${page}&limit=25`,
      { next: { revalidate: 3600 } }
    )
    if (!response.ok) throw new Error('Failed to fetch anime by genre')
    return response.json()
  } catch (error) {
    console.error('Error fetching anime by genre:', error)
    return { data: [] }
  }
}

// Search anime
export async function searchAnime(query: string, page = 1): Promise<AnimeResponse> {
  try {
    await rateLimit()
    const response = await fetch(
      `${JIKAN_API_BASE}/anime?query=${encodeURIComponent(query)}&page=${page}&limit=25`,
      { next: { revalidate: 3600 } }
    )
    if (!response.ok) throw new Error('Failed to search anime')
    return response.json()
  } catch (error) {
    console.error('Error searching anime:', error)
    return { data: [] }
  }
}

// Fetch anime detail
export async function getAnimeDetail(mal_id: number): Promise<Anime | null> {
  try {
    await rateLimit()
    const response = await fetch(`${JIKAN_API_BASE}/anime/${mal_id}`, {
      next: { revalidate: 3600 }
    })
    if (!response.ok) throw new Error('Failed to fetch anime detail')
    const data = await response.json()
    return data.data
  } catch (error) {
    console.error('Error fetching anime detail:', error)
    return null
  }
}

// Get genres list
export async function getGenres() {
  try {
    await rateLimit()
    const response = await fetch(`${JIKAN_API_BASE}/genres/anime`, {
      next: { revalidate: 86400 }
    })
    if (!response.ok) throw new Error('Failed to fetch genres')
    const data = await response.json()
    return data.data
  } catch (error) {
    console.error('Error fetching genres:', error)
    return []
  }
}
